	import service from "../../service.js";
	import headers from "../../components/header_nav.vue"
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		components: {
			uniLoadMore,
			headers
		},
		data() {
			return {
				Htitle: "核销记录",
				Hlicon: "../../static/icon/left_whitch.png",
				merchid:uni.getStorageSync("merchid"),
				funlist:[],//核销记录列表
					/* {
						id:1,
						coupon:{
							couponname:'好吃券',
							money:'32.6',
							type:1,
						},
						time:"2020-05-04 15：30：02"
					} */
				isLoading:false,
				scroll:false,
				status:0,
				pages:1,
				limit:15,
				page:0,
				out:0,
			}
		},
		onShow:function(){
			this.loadHistory();
		},
		onReachBottom() {
			this.loadHistory();
		},
        methods: {
			//加载历史数据
			loadHistory(e){
				if(this.isLoading){
					return ;
				}
				if(this.page < this.pages){
					this.out = 0;
					this.isLoading = true;
					this.scroll = false;
					this.status = 1;
					var page = this.page+1;
					service.auth(this, service.api.service.exchangelist, 'GET', {
						'page':page,
						'merchid':this.merchid,
					}, function(self, res) {
						if (res.code == 1) {
							var list = res.data.data;
							for(let i=0;i<list.length;i++){
								//将数据逐条写入数组
								self.funlist.push(list[i]);
							}
							self.pages = res.data.page_total;
							self.page = page;
							self.scroll = true;
							self.isLoading = false;
							/* setTimeout(function(){ */
								self.out = 1;
							/* },1000); */
							uni.setStorageSync('tasktime',1);
							if(self.page == self.pages || res.data.total == 0){
								self.status = 2;
							}else{
								self.status = 0;
							}
						}
							console.log(list)
					});
				}else{
					this.status = 2;
				}
			},
		}
	}
